import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ClassesService {

  constructor(public http:HttpClient) { }
  //get classes of a teacher
  getClassTeacher(id:any):Observable<any>{
    return this.http.post<any>('/api/class/teacherList',{'id':id});
  }
  //Teacher Create Class
  createClass(classData:any){
    return this.http.post('/api/class/create',{'class':classData});
  }
  //get all classes
  getClasses(){
    return this.http.get('/api/class/classList');
  }
  //Student enroll class
  enrollClass(classid:any,id:any){
    return this.http.post('/api/class/enrollClass',{'classid':classid,'id':id})
  }
  //list all enrolled classes of a student
  getClassStudent(id:any):Observable<any>{
    return this.http.post<any>('/api/class/studentlist',{'id':id});
  }
  //get single class details
  getSingleClass(id:any):Observable<any>{
    return this.http.get<any>('/api/class/'+id);
  }

  //update class details teacher
  updateClass(classData:any):Observable<any>{
    console.log(classData);
    return this.http.put<any>('/api/class/update',classData);
  }
  //delete a class
  deleteClass(id:any):Observable<any>{
    return this.http.delete('/api/class/delete/'+id);
  }


  //Create a note (teacher)
  createNote(id:any,noteData:any):Observable<any>{
    return this.http.post<any>('/api/class/createNote',{'noteData':noteData,'id':id});
  }

  //get notes of a class
  getNotes(classId:any):Observable<any>{
    return this.http.post<any>('/api/class/getNotes',{'classId':classId});
  }

  //delete a note
  deleteNote(id:any):Observable<any>{
    return this.http.delete('/api/class/deleteNote/'+id);
  }

  //create assignment
  createAssignment(id:any,assignmentData:any):Observable<any>{
    return this.http.post('/api/class/createAssignment',{'id':id,'assignmentData':assignmentData});
  }

  //get Assignments
  getAssignments(classId:any):Observable<any>{
    return this.http.post<any>('/api/class/getAssignments',{'classId':classId});
  }

  //delete Assignment
  deleteAssignment(id:any):Observable<any>{
    return this.http.delete('/api/class/deleteAssignment/'+id);
  }

  //submit assignment student
  submitAssignment(sid:any,asid:any,submission:any):Observable<any>{
    return this.http.post<any>('/api/class/submitAssignment',{'asid':asid,'submission':submission,'sId':sid});
  }

  //get single assignment details
  getAssignment(asid:any){
    return this.http.post('/api/class/getAssignment',{'asid':asid});
  }

  //get submissions of an Assignment
  getSubmissions(asid:any):Observable<any>{
    return this.http.post('/api/class/submissions',{'asid':asid});
  }

  //get submission status by student
  getSubmissionStatus(asid:any,sid:any):Observable<any>{
    return this.http.post<any>('/api/class/submissionStatus',{'asid':asid,'sid':sid});
  }

}
